import {NgModule} from '@angular/core';
import {APOLLO_OPTIONS} from 'apollo-angular';
import {ApolloClientOptions, ApolloLink, InMemoryCache} from '@apollo/client/core';
import {HttpLink} from 'apollo-angular/http';
import {setContext} from '@apollo/client/link/context';
import {AuthService} from './services/auth.service';
import {onError} from '@apollo/client/link/error';
import {environment} from '../environments/environment';

// const uri = 'http://64.225.107.140:3001/graphql'; // <-- add the URL of the GraphQL server here
const uri = environment.backendUrl; // <-- add the URL of the GraphQL server here

export function createApollo(httpLink: HttpLink, auths: AuthService): ApolloClientOptions<any> {

  const basic = setContext((operation, context) => ({
    headers: {
      Accept: 'en',
    }
  }));

  const auth = setContext((operation, context) => {
    const token = localStorage.getItem('token');
    const lang = localStorage.getItem('lang') || 'en';

    if (token === null) {
      return {};
    } else {

      return {
        headers: {
          Authorization: `Bearer ${token}`,
          'Accept-Language': lang
        },
      };
    }
  });
  const headerss = setContext((operation, context) => {
    const lang = localStorage.getItem('lang') || 'en';
    return {
      headers: {
        'Accept-Language': lang
      },
    };
  });

  const error = onError(({graphQLErrors, networkError, response, operation}) => {
    // Checking GraphQL Errors
    if (graphQLErrors) {
      graphQLErrors.map(({message, path}) => {
        if (message === 'INVALID_TOKEN') {
          localStorage.removeItem('token');
          window.location.href = '/';
        }
        if (message === 'UNAUTHENTICATED') {
          this.session.destroy();
          localStorage.removeItem('token');
          window.location.href = '/';
        }
        if (message === '["Unauthorized"]') {
          this.session.destroy();
          localStorage.removeItem('token');
          window.location.href = '/';

        }
        // localStorage.removeItem('token');
        console.log({
          interceptorError: `[GraphQL Error]: Type: ${message}, Message: ${message}`, path
        });
        // alert(`[GraphQL Error]: Type: ${message}, Message: ${message}`);
      });
    }
    // Checking in Network Errors
    if (networkError) {
    }
  });
  const link = ApolloLink.from([basic, headerss, auth, error, httpLink.create({uri})]);
  const cache = new InMemoryCache();

  return {
    link,
    cache
  };
}

@NgModule({
  providers: [
    {
      provide: APOLLO_OPTIONS,
      useFactory: createApollo,
      deps: [HttpLink, AuthService],

    },
  ],
})
export class GraphQLModule {
}
