import {gql} from 'apollo-angular';


export const navbarQuery = gql`
  query findManyServices{
    findManyChildServices {
      entities {
        parentService{id}
        id
        title
      }
    },
    findManyProducts{
      entities{
        id
        title
      }
    }
  },
`;
export const allProductsQuery = gql`
  query allProductsQuery{
    findManyProducts{
      entities{
        icon
        id
        title
        subTitle
        shortDesc
      }
    }
  }
`;
export const onePartner = gql`

  query oneProdQuery($id:Int!){
    findOnePartner(query: {id: $id}){
    id  title image  desc  onHome isActive
    }
  }
`;
export const oneFeature = gql`
  query oneFeature($id:Int!){
    findOneServiceFeature(query: {id: $id}){
    id  title image  desc
    }
  }
`;


export const oneProdQuery = gql`


  query oneProdQuery($id:Int!){
    findOneProduct(query: {id: $id}){
    id logo title icon  shortDesc subTitle onHome isActive
        smallParagraphs {
        fullDesc
        headline
      }
    }
    findManyProducts{
      entities{
        icon
        id
        title
        shortDesc
      }
    }
  }
`;

export const parentServiceQuery = gql`
  query parentServiceQuery{
    findManyParentServices {
      entities {
        id
        icon
        desc
        title
        childServices{id title icon features{title}}
      }
    }
  }
`;


export const findOneParentServiceQuery = gql`
  query findOneServiceQuery($id:Int!) {
  findOneParentService(query: {id: $id}) {
    id
    desc
    icon
    title
  }
  }
`;
export const findOneServiceQuery = gql`
  query findOneServiceQuery($id:Int!,$parentId:Int) {
    findOneChildService(query: {id: $id}) {
      id
      logo
      title
      fullDesc
      features{image desc title id}
    }
    findOneParentService(query: {id: $parentId}) {
      childServices{
        icon
        parentService{id}
        id
        logo
        title
      features{image desc title id}

      }
    }

  }
`;
export const findOneChildServiceQuery = gql`
  query findOneServiceQuery($id:Int!) {
    findOneChildService(query: {id: $id}) {
      id icon logo
      title
      fullDesc
      onHome isActive
    }
  }
`;

export const findServiceWithSameParent = gql`
  query findServiceWithSameParent($parentId:Int) {
    findOneParentService(query: {id: $parentId}) {
      childServices{
        parentService{id}
        id
        logo
        title
      }
    }
  }
`;
export const removeProduct = gql`

  mutation removeProduct($id:Int!){
    removeProduct(id: $id){id }
  }
`;
export const removePartner = gql`

  mutation removePartner($id:Int!){
    removePartner(id: $id){id }
  }
`;
export const removeServiceFeature = gql`

  mutation removeServiceFeature($id:Int!){
    removeServiceFeature(id: $id){id }
  }
`;
export const removeParentService = gql`

  mutation removeParentService($id:Int!){
    removeParentService(id: $id){id }
  }
`;
export const removeChildService = gql`

  mutation removeChildService($id:Int!){
    removeChildService(id: $id){id }
  }
`;

export const homeQuery = gql`

  query homeQuery{
    findManyParentServices {
      entities {
        icon
        id
        title
        childServices{id title}
      }
    },
    findManyProducts{
      entities{
        id
        title
        subTitle
        icon
        shortDesc

      }


    } ,
    findManyPartners{
      entities{
        id
        title
        image
        desc
      }


    }

  }
`;
