import {NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {AppComponent} from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {RouterModule, Routes} from '@angular/router';
import {FooterComponent} from './footer/footer.component';
import {SharedModule} from './shared/shared.module';
import {MainNavComponent} from './main-nav/main-nav.component';
import {LayoutModule} from '@angular/cdk/layout';
import {UserLayoutComponent} from './layouts/user-layout/user-layout.component';
import {SignInComponent} from './main/auth/signin/sign-in.component';
import {SignupComponent} from './main/auth/signup/signup.component';
import {ForgetPasswordComponent} from './main/auth/forget-password/forget-password.component';
import {ResetPasswordComponent} from './main/auth/reset-password/reset-password.component';
import {AuthLayoutComponent} from './layouts/auth-layout/auth-layout.component';
import {createApollo, GraphQLModule} from './graphql.module';
import {HttpClient, HttpClientModule} from '@angular/common/http';
import {APOLLO_OPTIONS} from 'apollo-angular';
import {HttpLink} from 'apollo-angular/http';
import {TermsComponent} from './terms/terms.component';
import {UserGuard} from './guard/user.guard';
// import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';

const appRoutes: Routes = [
  {
    path: '',
    component: UserLayoutComponent,
    children: [
      {
        path: 'home',
        loadChildren: () => import('./main/home/home.module').then(m => m.HomeModule), data: {preload: true}

      },
      {
        path: 'pricing',
        loadChildren: () => import('./main/pricing/pricing.module').then(m => m.PricingModule), data: {preload: false}
      }, {
        path: 'products',
        loadChildren: () => import('./main/products/products.module').then(m => m.ProductsModule), data: {preload: false}
      },
      {
        path: 'services',
        loadChildren: () => import('./main/services/services.module').then(m => m.ServicesModule), data: {preload: false}
      }, {
        path: 'about', data: {preload: false, animation: 'FilterPage'},
        loadChildren: () => import('./main/about/about.module').then(m => m.AboutModule)
      },
      {
        path: 'contact',
        data: {preload: false},
        loadChildren: () => import('./main/contact/contact.module').then(m => m.ContactModule),
      },
      {
        path: 'careers',
        loadChildren: () => import('./main/careers/careers.module').then(m => m.CareersModule), data: {preload: false}
      },
      {
        path: 'contact',
        loadChildren: () => import('./main/contact/contact.module').then(m => m.ContactModule), data: {preload: false}
      },
      {
        path: 'profile',
        loadChildren: () => import('./main/profile/profile.module').then(m => m.ProfileModule), data: {preload: false},
        canActivate: [UserGuard]
      }, {
        path: 'terms',
        component: TermsComponent
      },
      {
        path: '',
        redirectTo: 'home', pathMatch: 'full'
      }
    ]
  },
  {
    path: 'auth',
    component: AuthLayoutComponent,
    children: [
      {path: 'sign-in', component: SignInComponent},
      {path: 'sign-up', component: SignupComponent},
      {path: 'forget-password', component: ForgetPasswordComponent},
      {path: 'reset-password', component: ResetPasswordComponent},
      {path: '', redirectTo: 'sign-in', pathMatch: 'full'}
    ]
  },
  {
    path: '',
    redirectTo: '/home',
    pathMatch: 'full'
  }

];

@NgModule({
  declarations: [
    SignupComponent, SignInComponent, ForgetPasswordComponent, ResetPasswordComponent, AuthLayoutComponent,
    AppComponent,
    FooterComponent,
    MainNavComponent,
    UserLayoutComponent,
    TermsComponent,


  ],
  imports: [
    BrowserModule, SharedModule,
    RouterModule.forRoot(appRoutes, {scrollPositionRestoration: 'enabled'}),
    TranslateModule.forRoot({
      defaultLanguage: 'en',
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    BrowserAnimationsModule,
    LayoutModule,
    GraphQLModule,
    HttpClientModule,


  ],
  providers: [
    {
      provide: APOLLO_OPTIONS,
      useFactory: createApollo,
      deps: [HttpLink],
    },
  ],
  bootstrap: [AppComponent]
})

export class AppModule {
}

export function createTranslateLoader(http: HttpClient): TranslateHttpLoader {
  return new TranslateHttpLoader(http, 'assets/i18n/', '.json');
}
