import {Component, OnInit} from '@angular/core';
import {Apollo} from 'apollo-angular';
import {forgetPassword, signUp} from '../authMutation';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';

@Component({
  selector: 'app-forget-password',
  templateUrl: './forget-password.component.html',
  styleUrls: ['./forget-password.component.scss']
})
export class ForgetPasswordComponent implements OnInit {
  userData: FormGroup;

  constructor(private apollo: Apollo, private fb: FormBuilder, private router: Router) {
  }

  ngOnInit(): void {

    this.userData = this.fb.group({
      email: ['', [Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
    });
  }

  send(): void {
    this.apollo
      .mutate({
        mutation: forgetPassword,
        variables: this.userData.value
      })
      .subscribe(
        (result: any) => {
          if (result.data.userForgetPassword) {
            alert('check your email to reset password');
            this.router.navigateByUrl('/auth/sign-in');
          } else {
            alert('Error , email not found');
          }
        }, (error => alert(error))
      );
  }


}
