import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {MatFormFieldModule} from '@angular/material/form-field';

import {MatCardModule} from '@angular/material/card';
import {MatCheckboxModule} from '@angular/material/checkbox';
import {FlexLayoutModule} from '@angular/flex-layout';
import {MatIconModule} from '@angular/material/icon';
import {MatButtonModule} from '@angular/material/button';
import {MatSidenavModule} from '@angular/material/sidenav';
import {RouterModule} from '@angular/router';
import {MatToolbarModule} from '@angular/material/toolbar';
import {MatListModule} from '@angular/material/list';
import {MatExpansionModule} from '@angular/material/expansion';
import {CarouselModule} from 'ngx-owl-carousel-o';
import {MatMenuModule} from '@angular/material/menu';
import {MatDividerModule} from '@angular/material/divider';
import {MatRadioModule} from '@angular/material/radio';
import {MatStepperModule} from '@angular/material/stepper';
import {HttpClientModule} from '@angular/common/http';
import {MatChipsModule} from '@angular/material/chips';
import {LoadingAnimationComponent} from './loading-animation/loading-animation.component';
import {LottieModule} from 'ngx-lottie';
import {playerFactory} from '../main/pricing/pricing.module';
import {MatDialogModule} from '@angular/material/dialog';
import {DisableControlDirective} from '../main/profile/profile/disable-control.directive';
import {HighlightDirective} from './highlight.directive';
import {TrancatePipe} from './trancate.pipe';
import {TrancateWordsPipe} from './trancate-words.pipe';
import {TranslateModule} from '@ngx-translate/core';
import {LetsTalkComponent} from './lets-talk/lets-talk.component';

const PIPES = [TrancatePipe, TrancateWordsPipe];
const modulesToExport = [TranslateModule, MatFormFieldModule,
  CommonModule, MatMenuModule, MatDividerModule, MatChipsModule, MatExpansionModule,
  FormsModule,
  ReactiveFormsModule, HttpClientModule,
  MatCardModule,
  MatCheckboxModule,
  MatIconModule, MatButtonModule,
  MatSidenavModule,
  FlexLayoutModule,
  RouterModule,
  MatToolbarModule,
  MatButtonModule,
  MatSidenavModule,
  MatIconModule,
  MatListModule, CarouselModule, MatRadioModule, MatStepperModule
  , MatDialogModule
];
const directives = [DisableControlDirective, HighlightDirective];

@NgModule({
  declarations: [...directives, ...PIPES, LetsTalkComponent],
  imports: [...modulesToExport],
  exports: [...modulesToExport, ...directives, ...PIPES, LetsTalkComponent],
})
export class SharedModule {
}
