import {Component} from '@angular/core';
import {BreakpointObserver, Breakpoints} from '@angular/cdk/layout';
import {Observable} from 'rxjs';
import {map, shareReplay} from 'rxjs/operators';
import {Apollo, gql} from 'apollo-angular';
import {navbarQuery} from '../main/home/home/homequeries';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import {AuthService} from '../services/auth.service';
import {Router} from '@angular/router';
import {MatIconRegistry} from '@angular/material/icon';
import {DomSanitizer} from '@angular/platform-browser';
import {TranslateModule, TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-main-nav',
  templateUrl: './main-nav.component.html',
  styleUrls: ['./main-nav.component.scss'],
  animations: [
    trigger('animation', [
      state('show', style({
        opacity: 1,
        transform: 'translateY(100)'
      })),
      state('hide', style({
        opacity: 0,
        transform: 'translateY(-100%)'
      })),
      transition('show => hide', animate('350ms ease-out')),
      transition('hide => show', animate('350ms ease-in'))
    ])
  ]
})
export class MainNavComponent {
  state = 'show';
  loading = true;
  hideUserMenu = true;
  isLogged: boolean;
  services: any[] = [];
  products: any[] = [];
  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches),
      shareReplay()
    );
  isWeb$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Large)
    .pipe(
      map(result => result.matches),
      shareReplay()
    );
  lang = localStorage.getItem('lang') || 'en';

  constructor(private breakpointObserver: BreakpointObserver,
              private matIconRegistry: MatIconRegistry,
              private domSanitizer: DomSanitizer,
              private apollo: Apollo,
              public authServ: AuthService,
              private router: Router,
              private translate: TranslateService) {
    // add svg icons
    this.checkLogged();
    this.addIcons();
    this.getData();
    // language settings
    translate.addLangs(['en', 'ar']);
    this.translate.setDefaultLang(this.lang);
    this.translate.use(this.lang);

  }

  getData(): void {
    this.apollo
      .watchQuery({
        query: navbarQuery,
        fetchPolicy: 'network-only'
      })
      .valueChanges.subscribe((result: any) => {
      this.services = result?.data?.findManyChildServices?.entities;
      this.products = result?.data?.findManyProducts?.entities;
      this.loading = result.loading;
    });
  }

  addIcons(): void {
    // this.matIconRegistry.addSvgIcon(
    //   `userColors`,
    //   this.domSanitizer.bypassSecurityTrustResourceUrl('/assets/icons/userColors.svg')
    // );
    this.matIconRegistry.addSvgIcon(
      `ar`,
      this.domSanitizer.bypassSecurityTrustResourceUrl('/assets/icons/ar.svg')
    );
  }

  isEnglish(): boolean {
    const lang = localStorage.getItem('lang');
    if (lang === 'en') {
      return true;
    } else {
      return false;
    }
  }

  setLang(lang): void {
    localStorage.setItem('lang', lang);
    this.translate.use(lang);
    window.location.reload();
  }

  navigate(): void {
    this.router.navigate(['pricing/request-offer/1']);
  }

  checkLogged(): void {
    this.authServ.logged().subscribe((logged) => {
        this.isLogged = logged;
        if (this.isLogged === false) {
          // this.authServ.destoryCredentials();

        }
      }
    );
  }

  logOut(): void {
    this.authServ.signOut();
  }
}
