import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {Apollo, gql} from 'apollo-angular';
import {getUser, signIn, signUp} from '../main/auth/authMutation';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  isAuthenticated: BehaviorSubject<boolean> = new BehaviorSubject(false);
  isAdmin: BehaviorSubject<boolean> = new BehaviorSubject(false);

  // userName: BehaviorSubject<string> = new BehaviorSubject('');

  constructor(private apollo: Apollo) {
    if (localStorage.getItem('token')) {
      this.isAuthenticated.next(true);
    } else {
      this.isAuthenticated.next(false);
    }
  }

  checkAdmin() {
    this.apollo
      .query({
        query: getUser,
        fetchPolicy: 'network-only'
      })
      .subscribe(
        (result: any) => {
          if (result.data.me.roles[0] === 'admin') {
            this.isAdmin.next(true);
          } else {
            this.isAdmin.next(false);
          }
        },
        error => {
          this.isAdmin.next(false);
        }
      );
    return this.isAdmin;
  }

  checkvalidtoken() {
    this.apollo
      .query({
        query: getUser,
        fetchPolicy: 'network-only'
      })
      .subscribe(
        (result: any) => {
          if (result.data.me) {
            this.isAuthenticated.next(true);
          } else {
            this.signOut();
            this.isAuthenticated.next(false);
          }
        },
        error => {
          this.signOut();

          this.isAuthenticated.next(false);
        }
      );
    return this.isAuthenticated;
  }

  logged(): BehaviorSubject<any> {
    return this.isAuthenticated;
  }

  isAdminUser(): BehaviorSubject<any> {
    return this.isAdmin;
  }

  signIn(userData: any): void {
    this.apollo
      .mutate({
        mutation: signIn,
        variables: userData
      })
      .subscribe(
        (result: any) => {
          localStorage.setItem('token', result.data.userLogin.token);
          this.isAuthenticated.next(true);

          // this.userName.next(String(result.data.userLogin.user.name));
          window.location.href = '/';
        },
        error => {
          alert(error);
        }
      );
  }

  register(userData: any): void {
    this.apollo
      .mutate({
        mutation: signUp,
        variables: userData
      })
      .subscribe(
        (result: any) => {
          localStorage.setItem('token', result.data?.userRegister?.token);
          this.isAuthenticated.next(true);
          window.location.href = '/';
        }, (error => alert(error))
      );
  }

  signOut(): void {
    localStorage.removeItem('token');
    this.isAuthenticated.next(false);
    // this.userName.next('');
    window.location.href = '/';
  }

  destoryCredentials(): void {
    localStorage.removeItem('token');
    this.isAuthenticated.next(false);
  }


}
